.healcode-contract-text-link,
.healcode-pricing-option-text-link {
  padding-bottom: 41px !important;
  color: #fff;
  transition: all 0.3s cubic-bezier(0.49, 0.99, 0.54, 0.98);

  &:hover {
    color: #fff;
    text-decoration: none;
  }
}

div.healcode div.hc_footer {
  display: none !important;
}

div.list_view li.odd {
  background: inherit !important;
}

.week_links>span a {
  background: #fff !important;
  font-size: 11px !important;
}

.healcode-description-area {
  font-size: .90rem;
  font-weight: 300;
  line-height: 1.35;
  color: #333E47;

  div {
    font-size: 1.15rem;
    line-height: 1.5;
  }
}

.ctct-custom-form {
  padding: 15px 15px 15px 0 !important;
}

div.healcode div.hc_footer * {
  width: 1px;
}

.ui-datepicker-inline {
  width: 100% !important;
}

.massage--rates_calendar div.healcode a.hc-button, div.healcode input.hc-button {
  padding: 1em 1.75em !important;
  font-size: 16px !important;
}